import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'

const Bio = () => {
    return(
        <Container as='section' fluid='lg' className='boxed-layout p-5'>
            <Row>
                <Col>
                    <h1 style={{color: '#033249', textDecoration: 'underline'}}>Bio</h1>
                </Col>
            </Row>
        </Container>
    )
}

export default Bio